import helangCheckbox from "@/components/helang-checkbox/helang-checkbox.vue"
export default {
	components: {
	    "helang-checkbox":helangCheckbox
	},
	data() {
		return {
			title:'续费',
			pickerIndex:-1,
			list: [{
				id:1,
				text:'一周',
				sontext:'30元'
			},
			{
				id:2,
				text:'一个月',
				sontext:'150元'
			},
			{
				id:3,
				text:'半年',
				sontext:'300元'
			},
			{
				id:4,
				text:'一年',
				sontext:'500元'
			}
			]
		}
	},
	onLoad(){
		
	},
	onShow(){
		this.$refs.checkbox.set({
			type:'radio',		// 类型：复选框
			column:2,				// 分列：3
			list:this.list	// 列表数据
		});
		this.pickerIndex = 3;
	},
	methods: {
		/* 创建列表数据 */
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		creatorList(){
			let arr=[];
			for(let i=1; i<16;i++){
				arr.push({
					id:i,
					text:`第${i}项`,
					sontext:'30元'
				});
			}
			return arr;
		},
		
		/* 获取数据 */
		getData(){
			let data = this.$refs.checkbox.get();	// 组件返回的数据
			uni.showToast({
				title:'在控制台看数据',
				icon:'none',
				duration:1000,
				mask:false,
				success:()=>{
					console.log(data);
				}
			});
		},
		/* 选项切换事件 */
		onChange(data){
			console.log(data);
		},
		/* 全选 */
		check(){
			this.$refs.checkbox.checkAll();		// 该功能在单选框模式下无效
			this.getData();
		},
		/* 取消全选 */
		cancel(){
			this.$refs.checkbox.cancelAll();	// 该功能在单选框模式下为取消当前选中项
			this.getData();
		},
		/* 反选 */
		invert(){
			this.$refs.checkbox.invertAll();	// 该功能在单选框模式下为取消当前选中项
			this.getData();
		}
	}
}
